body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.layout {
  min-height: 100vh; /* Set the minimum height to fill the viewport */
  display: flex; /* Use flexbox layout */
  flex-direction: column; /* Set the flexbox direction to column */
  justify-content: flex-start; /* Align content to the top of the container */
  align-items: center; /* Align items to the center of the container horizontally */
  background-color: #212121;
  color: #fff;
  font-family: 'Montserrat', sans-serif;
}

.textfield {
  box-sizing: border-box;
  width: 300px;
  height: 45px;
  border: 1px solid #0000;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 16px;
}

.button {
  background-color: #007bff;
  color: #fff;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 16px;
  cursor: pointer;
}

.button.login-button {
  margin-top: 20px;
  width: 300px;
  height: 45px;
}
.forgotpassword {
  position: absolute;
  width: 146px;
  height: 20px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #FFFFFF;
  cursor: pointer;
}
.loginpage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.form {
  position: absolute;
  width: 300px;
  height: 398px;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #1a1a1a;
  padding: 20px;
  border-radius: 10px;
}

.textfield {
  box-sizing: border-box;
  width: 100%;
  height: 45px;
  border: 1px solid #FFFFFF;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 16px;
  margin-bottom: 20px;
  color: #fff;
