  color: #fff;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 16px;
  cursor: pointer;
  margin-top: 20px;
}

.custom-forgotpassword {
  position: absolute;
  width: 146px;
  height: 20px;
  left: 50%;
  bottom: 20px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  color: #fff;
  cursor: pointer;
}
