.layout {
  min-height: 100vh; /* Set the minimum height to fill the viewport */
  display: flex; /* Use flexbox layout */
  flex-direction: column; /* Set the flexbox direction to column */
  justify-content: flex-start; /* Align content to the top of the container */
  align-items: center; /* Align items to the center of the container horizontally */
  background-color: #212121;
  color: #fff;
  font-family: 'Montserrat', sans-serif;
}
