.loginpage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.form {
  position: absolute;
  width: 300px;
  height: 398px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #1a1a1a;
  padding: 20px;
  border-radius: 10px;
}

.textfield {
  box-sizing: border-box;
  width: 100%;
  height: 45px;
  border: 1px solid #FFFFFF;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 16px;
  margin-bottom: 20px;
  color: #fff;
  background-color: #1a1a1a;
}

.button.login-button {
  background-color: #007bff;
  color: #fff;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 16px;
  cursor: pointer;
  margin-top: 20px;
}

.custom-forgotpassword {
  position: absolute;
  width: 146px;
  height: 20px;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
  color: #fff;
  cursor: pointer;
}